///* Twitter
//----------------------------------------------------------*/
.twitter-feed ul {
  list-style: none;
  margin: 0 0 0 0;
  padding: 0;
}

.twitter-feed ul li {
  position: relative;
  margin-bottom: 30px;
  display: flex
}

.twitter-feed ul li a {
  //font-weight: 600;
  color: inherit;
}

.twitter-feed ul li .tztwd-tweet-data {
  display: block;
  //font-size: 95%;
  //filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=50);
  //opacity: 0.5;
  //margin-top: 4px;
  color: #e4e4e4;
  margin-top: 9px;
}

.twitter-feed {
  .avata-info {
    font-size: 120%;
    color: #ff5400;
    padding-top: 4px;
    i {
      color: #ff5400;
    }
  }
  .tz-content_tweet {
    //padding-top: 4px;
    padding-left: 15px;
    color: #868686;
    line-height: 23px;
    overflow: hidden;
    a {
      color: #e4e4e4;
      text-decoration: none;
    }
  }
  .tztwd-tweet-data {
    display: block;
  }
}

