.tz__absolute {
  position: absolute !important;
}

.tz__footer--center {
  left: 50%;
  text-align: center;
  margin: auto;
  .translate(-50%, 0);
  top: -59px;
  width: auto !important;
}

@keyframes autoshowroom_rotate {
  from {
    transform: rotateY(0);
    -webkit-transform: rotateY(0);
    -moz-transform: rotateY(0);
    -ms-transform: rotateY(0);
    -o-transform: rotateY(0);
  }
  to {
    transform: rotateY(360deg);
    -webkit-transform: rotateY(360deg);
    -moz-transform: rotateY(360deg);
    -ms-transform: rotateY(360deg);
    -o-transform: rotateY(360deg);
  }
}

/* Forms
----------------------------------------------------------*/
.form-control {
  -moz-border-radius: 0;
  -webkit-border-radius: 0;
  border-radius: 0;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  background-color: #ededed;
  font-size: 13px;
  padding: 15px 20px;
  height: 46px;
  border: 1px solid #ededed;
  margin-bottom: 0;
  -moz-appearance: none;
  -webkit-appearance: none;
  .transition(all 0.3s linear 0s);
}

.form-control:-moz-placeholder {
  color: inherit;
  opacity: 0.7;
}

.form-control::-moz-placeholder {
  color: inherit;
  opacity: 0.7;
}

.form-control:-ms-input-placeholder {
  color: inherit;
  opacity: 0.7;
}

.form-control::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.7;
}

.form-control:focus, .form-control:active {
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  border: 1px solid #d2d2d2;
}

.form-control.input-2 {
  -moz-border-radius: 30px;
  -webkit-border-radius: 30px;
  border-radius: 30px;
}

.form-control.input-sm {
  padding: 6px 13px;
  height: 38px;
  font-size: 12px;
}

.form-control.input-lg {
  padding: 0 10px;
  height: 38px;
  font-size: 14px;
  line-height: 38px;
}

.form-control.bordered {
  background: transparent !important;
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.form-control.bordered:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-control.bordered::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-control.bordered:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-control.bordered::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-control.bordered:focus, .form-control.bordered:active {
  border-color: rgba(255, 255, 255, 0.4);
}

.form-control.valid {
  border-color: #8ac04b;
}

.form-control.error {
  border-color: #e15757;
}

.form-control[type="submit"] {
  background-color: #444;
  background-image: none;
  text-shadow: none;
  color: #fff;
  &:hover {
    background: #ffffff;
    color: #444444;
  }
}

.form-control.bg-transparent {
  background: transparent;
  color: #565656;
}

textarea.input-sm {
  height: auto !important;
}

.dark .form-control {
  background-color: #fff;
}

// list
.list-inline > li {
  display: inline-block !important;
}

/* Button */
.btn {
  .transition(all 0.3s linear 0s);
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  font-size: 12px;
  font-family: 'Montserrat', sans-serif, Arial, sans-serif;
  padding: 10px;
}

.btn-primary {
  background: #ff5400;
  border-color: #ff5400;
}